@import url(https://fonts.googleapis.com/css?family=Playfair+Display);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  font-family: 'Playfair Display', serif;
  padding-bottom: 3rem;
  color: #4d4848; }

button {
  font-weight: bold !important; }

.App-inner {
  max-width: 1200px;
  margin: 0 auto; }

p {
  padding: 0;
  margin: 0; }

nav {
  padding: 3rem 0;
  background: #c7c7c7;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  box-shadow: 0px 9px 5px -5px rgba(0, 0, 0, 0.3); }
  nav h1, nav h3 {
    color: white; }
  nav .nav-header {
    text-align: center;
    padding: 0 1rem;
    color: #141414; }
    nav .nav-header .quote-auth {
      font-size: 14px;
      margin-top: 1rem; }

sup {
  font-size: 19px; }

.body .quick-weather {
  padding: 0 1rem; }

.body .quick-city-body:hover {
  background: #c1d1e14f;
  cursor: pointer; }

.body .quick-city__name {
  font-weight: bold;
  color: #4d4848; }

.body .quick-city__temp {
  font-size: 30px;
  color: #4d4848;
  margin-right: 7px; }
  .body .quick-city__temp sup {
    font-size: 13px; }

.body .user-temp {
  position: relative;
  background: #f1f7f8;
  padding: 1rem; }
  .body .user-temp .user-temp__main {
    font-size: 8rem; }
  .body .user-temp .user-temp__detail {
    display: flex;
    flex-direction: column;
    margin-left: 1rem;
    margin-top: 24px; }
    .body .user-temp .user-temp__detail .user-temp__desc {
      font-size: 30px;
      color: #675f5f; }
    .body .user-temp .user-temp__detail .user-temp__desc2 {
      font-size: 16px;
      color: #969696; }
    .body .user-temp .user-temp__detail .user-temp__desc3 {
      display: none; }

form {
  display: flex;
  flex-direction: column;
  width: 70%; }
  form #input {
    font-size: 2rem;
    font-family: 'Playfair Display', serif;
    text-align: center;
    color: white; }
  form .btn-submit {
    width: 25%;
    margin: 0 auto;
    margin-top: 1rem;
    font-weight: bold;
    background-color: #3fb596; }
    form .btn-submit:hover {
      background-color: #38a78a; }

.quick-cities {
  display: flex; }

.user-temp {
  margin: 5rem 0;
  display: block;
  justify-content: space-between; }
  .user-temp .btn-save {
    align-self: flex-start;
    position: absolute;
    right: 33px; }
  .user-temp .user-temp__body, .user-temp .user-temp__side {
    flex: 1 1; }
    .user-temp .user-temp__body .user-temp__location, .user-temp .user-temp__side .user-temp__location {
      font-size: 30px;
      font-weight: normal; }
    .user-temp .user-temp__body .user-temp__location,
    .user-temp .user-temp__body .user-temp__date, .user-temp .user-temp__side .user-temp__location,
    .user-temp .user-temp__side .user-temp__date {
      margin: 0;
      padding: 0; }
    .user-temp .user-temp__body .user-temp__date, .user-temp .user-temp__side .user-temp__date {
      font-size: 14px;
      color: #878787;
      font-weight: normal; }
    .user-temp .user-temp__body .user-temp__fiveday, .user-temp .user-temp__side .user-temp__fiveday {
      display: flex;
      justify-content: space-around;
      text-align: center;
      margin: 4rem 0 2rem 0; }
      .user-temp .user-temp__body .user-temp__fiveday .forecast-date, .user-temp .user-temp__side .user-temp__fiveday .forecast-date {
        color: #4d4848;
        font-size: 14px; }
  .user-temp .user-temp__body .user-temp__inner {
    display: flex;
    align-items: center;
    justify-content: center; }

.slick-arrow.slick-next:before,
.slick-arrow.slick-prev:before {
  color: #cecece; }

.wi {
  font-size: 2rem;
  color: #51657b; }

.slick-track {
  margin: 0;
  text-align: center; }

.x {
  display: flex;
  justify-content: center;
  flex-direction: column; }

.quick-city__weather-temp {
  text-align: left;
  line-height: 1;
  display: flex;
  align-items: center; }

.quick-city__weather-desc {
  display: flex;
  flex-wrap: wrap;
  line-height: 1;
  align-items: center;
  justify-content: center;
  color: #828282;
  margin: 0 auto;
  width: 26%;
  font-size: 14px; }

.toggle {
  justify-content: flex-end; }

.no-results {
  text-align: center;
  font-size: 40px;
  padding-top: 10rem; }

.slick-slider {
  width: 80%;
  margin: 0 auto; }

@media only screen and (min-width: 1200px) {
  nav .nav-header p {
    font-size: 2rem; }
  nav .nav-header .quote-auth {
    font-size: 20px;
    margin-top: 1rem;
    float: right; }
  form {
    width: 40%; }
    form #input {
      font-size: 3rem; }
  .wi {
    font-size: 4rem; }
  sup {
    font-size: 30px; }
  .body .user-temp {
    display: flex;
    padding: 2rem; }
    .body .user-temp .user-temp__main {
      font-size: 10rem;
      color: #4d4848; }
    .body .user-temp .user-temp__detail .user-temp__desc {
      font-size: 40px; }
    .body .user-temp .user-temp__detail .user-temp__desc2 {
      font-size: 16px; }
    .body .user-temp .user-temp__detail .user-temp__desc3 {
      display: flex; }
      .body .user-temp .user-temp__detail .user-temp__desc3 p {
        font-size: 14px;
        color: #838383; }
      .body .user-temp .user-temp__detail .user-temp__desc3 span {
        font-size: 20px; }
      .body .user-temp .user-temp__detail .user-temp__desc3 sup {
        font-size: 13px; }
      .body .user-temp .user-temp__detail .user-temp__desc3 .user-temp__high {
        margin-right: 0; }
    .body .user-temp .user-temp__detail .user-temp__high {
      margin-right: 1rem; }
    .body .user-temp .user-temp__body {
      border-right: 1px solid #85919f40; }
      .body .user-temp .user-temp__body .user-temp__inner {
        display: flex;
        height: 100%;
        position: absolute;
        top: 0;
        justify-content: center;
        width: 44%; }
    .body .user-temp .user-temp__side {
      padding-left: 2rem; } }

